import { Table } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { addCount, decreaseCount, deleteItem, sortName } from './store'
import { Button } from 'react-bootstrap'
import { Link } from 'react-router-dom';
import "./Cart.css"


interface RootState {
    user: {
      name: string;
      age: number;
    };
    cart: {
      id: number;
      imgurl: string;
      name: string;
      count: number;
    }[];
  }

function Cart(): JSX.Element {

    let state = useSelector((state: RootState) => state)
    // console.log(state.cart[0].name);

    // dispatch는  store.js 로 요청보내주는 함수
    let dispatch = useDispatch()


    const smallProdcuctStyle: React.CSSProperties = {
        border: '1px solid #ddd',
        width: '100px',
        height: '80px',
        cursor: 'pointer'
    };

    let textverticalAlign: React.CSSProperties = {
        verticalAlign: 'middle',
        textAlign: 'center'
    }

    return (
        <>
            <h5 style={{ padding: '50px' }}>🤍 {state.user.name} {state.user.age}의 장바구니</h5>
            {/* <button onClick={()=>{ dispatch(increase())}}>나이+</button>
       <button onClick={()=>{ dispatch(increase(100))}}>나이+</button> */}


            <Table>
                <thead>
                    <tr>
                        <th>id</th>
                        <th>상품이미지</th>
                        <th>상품명</th>
                        <th>수량</th>
                        <th>변경하기</th>
                    </tr>
                </thead>
                <tbody>


                    {
                        state.cart.map((a, i) =>
                            <tr key={i}>
                                <td style={textverticalAlign}>{state.cart[i].id}</td>


                                <td>

                                    <Link to={`/detail/${state.cart[i].id}`}>
                                        <img src={`img/${state.cart[i].imgurl}`} style={smallProdcuctStyle} />
                                    </Link>

                                </td>
                                <td style={textverticalAlign}>{state.cart[i].name}</td>
                                <td className='cartcount' style={textverticalAlign}>{state.cart[i].count}</td>
                                <td style={textverticalAlign}>
                                    <Button onClick={() => {
                                        dispatch(addCount(state.cart[i].id))
                                    }} variant="Outline-success" style={{ marginRight: '10px' }}>+</Button>

                                    <Button onClick={() => {
                                        dispatch(decreaseCount(state.cart[i].id))
                                    }} variant="outline-warning" style={{ marginRight: '10px' }}>-</Button>

                                    <Button onClick={() => {
                                        dispatch(deleteItem(state.cart[i].id))
                                    }} variant="outline-danger">상품삭제</Button>

                                </td>
                            </tr>
                        )
                    }

                </tbody>

            </Table>
            <Button variant="outline-primary" onClick={() => {
                dispatch(sortName());
            }}>이름순정렬</Button>{' '}
        </>
    )
}

export default Cart