import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import { Link } from 'react-router-dom';
// import required modules
import { Autoplay, Pagination, Navigation } from "swiper";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import "./Brandfocus.css";
import focus_data1 from "./BfocusData1";
import focus_data2 from "./BfocusData1_2";
import focus_data3 from "./BfocusData2";
import focus_data4 from "./BfocusData2_2";
import focus_data5 from "./BfocusData3";
import focus_data6 from "./BfocusData3_2";

// type import
import {Brandfocus1} from "./BfocusData1";
import {Brandfocus1_2} from "./BfocusData1_2";
import {Brandfocus2} from "./BfocusData2";
import {Brandfocus2_2} from "./BfocusData2_2";
import {Brandfocus3} from "./BfocusData3";
import {Brandfocus3_2} from "./BfocusData3_2";


interface Focus1_2Props {
    bfdata1 : Brandfocus1[];
    bfdata2 : Brandfocus1_2[];
    res2 : number[]

}
interface Focus3_4Props {
    bfdata3 : Brandfocus2[];
    bfdata4 : Brandfocus2_2[];
    res4 : number[]
}
interface Focus5_6Props {
    bfdata5 : Brandfocus3[];
    bfdata6 : Brandfocus3_2[];
    res6 : number[]
}



function Brandfocus() {
    const [bfdata1] = useState(focus_data1);
    const [bfdata2] = useState(focus_data2);
    const [bfdata3] = useState(focus_data3);
    const [bfdata4] = useState(focus_data4);
    const [bfdata5] = useState(focus_data5);
    const [bfdata6] = useState(focus_data6);


    let [res2, setRes2] = useState([34, 35, 36, 37, 38, 39]);
    let [res4, setRes4] = useState([42, 43, 44, 45, 46, 47]);
    let [res6, setRes6] = useState([50, 51, 52, 53, 54, 55]);


    return (
        <>
            <Swiper
                spaceBetween={30}
                centeredSlides={true}
                autoplay={{
                    delay: 2500,
                    disableOnInteraction: false,
                }}
                pagination={{
                    clickable: true,
                }}
                navigation={true}
                modules={[Autoplay, Pagination, Navigation]}
                className="mySwiper3"
            >
                <SwiperSlide>
                    <Bfocusbox1 bfdata1={bfdata1} bfdata2={bfdata2}  res2={res2}/>
                </SwiperSlide>
                <SwiperSlide>
                    <Bfocusbox2 bfdata3={bfdata3} bfdata4={bfdata4}  res4={res4}/>
                </SwiperSlide>
                <SwiperSlide>
                    <Bfocusbox3 bfdata5={bfdata5} bfdata6={bfdata6} res6={res6}/>
                </SwiperSlide>
            </Swiper>
        </>
    );
}



function Bfocusbox1(props:Focus1_2Props):JSX.Element {
    return (
        <>
            {
                props.bfdata1.map((a:Brandfocus1, i:number) => {
                    return (
                        <div className="bfBox_top">
                            <img src={a.imgurl} />
                        </div>
                    )
                })
            }
            {
                props.bfdata2.map((a:Brandfocus1_2, i) => {
                    return (
                        <div className="bfBox_bottom">
                            <Link to={`/detail/${props.res2[i]}`}>
                            <img src={a.imgurl} />
                            <div className="bfBox_txt">
                                <h6>{a.brand}</h6>
                                <p>{a.title}</p>
                                <span>{a.persent}</span><span>{a.price}</span>
                            </div>
                            </Link>
                        </div>
                    )
                })
            }
        </>
    )
}


function Bfocusbox2(props:Focus3_4Props):JSX.Element {
    return (
        <>
            {
                props.bfdata3.map((a:Brandfocus2, i:number) => {
                    return (
                        <div className="bfBox_top">
                            <img src={a.imgurl} />
                        </div>
                    )
                })
            }
            {
                props.bfdata4.map((a:Brandfocus2_2, i) => {
                    return (
                        <div className="bfBox_bottom">
                            <Link to={`/detail/${props.res4[i]}`}>
                            <img src={a.imgurl} />
                            <div className="bfBox_txt">
                                <h6>{a.brand}</h6>
                                <p>{a.title}</p>
                                <span>{a.persent}</span><span>{a.price}</span>
                            </div>
                            </Link>
                        </div>
                    )
                })
            }
        </>
    )
}


function Bfocusbox3(props:Focus5_6Props):JSX.Element {
    return (
        <>
            {
                props.bfdata5.map((a:Brandfocus3, i:number) => {
                    return (
                        <div className="bfBox_top">
                            <img src={a.imgurl} />
                        </div>
                    )
                })
            }
            {
                props.bfdata6.map((a:Brandfocus3_2, i) => {
                    return (
                        <div className="bfBox_bottom">
                            <Link to={`/detail/${props.res6[i]}`}>
                            <img src={a.imgurl} />
                            <div className="bfBox_txt">
                                <h6>{a.brand}</h6>
                                <p>{a.title}</p>
                                <span>{a.persent}</span><span>{a.price}</span>
                            </div>
                            </Link>
                        </div>
                    )
                })
            }
        </>
    )
}

export default Brandfocus;
