import React, { useRef, useState } from "react";
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import { Link } from 'react-router-dom';

import "swiper/css/navigation";
import "./BestPick.css";


import { Navigation } from "swiper";

// Data
import BestPick1 from './pickData1';
import BestPick2 from './pickData2';
import BestPick3 from './pickData3';
import BestPick4 from './pickData4';
import BestPick5 from './pickData5';

import {TabPanelProps} from "./NewArrival"

// type import
import {Pick1} from './pickData1';
import {Pick2} from './pickData2';
import {Pick3} from './pickData3';
import {Pick4} from './pickData4';
import {Pick5} from './pickData5';

interface PickProps1 {
    data1 : Pick1[];
    res1 : number[];
}
interface PickProps2 {
    data2 : Pick2[];
    res2 : number[];
}
interface PickProps3 {
    data3 : Pick3[];
    res3 : number[];
}
interface PickProps4 {
    data4 : Pick4[];
    res4 : number[];
}
interface PickProps5 {
    data5 : Pick5[];
    res5 : number[];
}



function BestPick():JSX.Element {
    const [value, setValue] = React.useState(0);

    const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
        setValue(newValue);
    };

    let [data1] = useState(BestPick1)
    let [data2] = useState(BestPick2)
    let [data3] = useState(BestPick3)
    let [data4] = useState(BestPick4)
    let [data5] = useState(BestPick5)

    let [res1] = useState([72, 73, 74, 75, 76, 77, 78, 79]);
    let [res2] = useState([80, 81, 82, 83, 84, 85, 86, 87]);
    let [res3] = useState([88, 89, 90, 91, 92, 93, 94, 95]);
    let [res4] = useState([96, 97, 98, 99, 100, 101, 102, 103]);
    let [res5] = useState([104, 105, 106, 107, 108, 109, 110, 111]);

    return (
        <Box sx={{ width: '100%' }}>
            <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
                    <Tab label="전체" {...a11yProps(0)} />
                    <Tab label="여성" {...a11yProps(1)} />
                    <Tab label="남성" {...a11yProps(2)} />
                    <Tab label="잡화/슈즈" {...a11yProps(3)} />
                    <Tab label="골프/스포츠" {...a11yProps(4)} />
                </Tabs>
            </Box>
            <TabPanel value={value} index={0} style={{ width: '1240px', margin: '0 auto' }}>
                <div className="Pickboxwrap">
                    <AllBox data1={data1} res1={res1} />
                </div>
            </TabPanel>

            <TabPanel value={value} index={1} style={{ width: '1240px', margin: '0 auto' }}>
                <div className="Pickboxwrap">
                    <WomenBox data2={data2} res2={res2} />
                </div>

            </TabPanel>

            <TabPanel value={value} index={2} style={{ width: '1240px', margin: '0 auto' }}>
                <div className="Pickboxwrap">
                    <MenBox data3={data3} res3={res3} />
                </div>

            </TabPanel>

            <TabPanel value={value} index={3} style={{ width: '1240px', margin: '0 auto' }}>
                <div className="Pickboxwrap">
                    <ShoeBox data4={data4} res4={res4} />
                </div>

            </TabPanel>

            <TabPanel value={value} index={4} style={{ width: '1240px', margin: '0 auto' }}>
                <div className="Pickboxwrap">
                    <GolfBox data5={data5} res5={res5} />
                </div>

            </TabPanel>
        </Box>
    );
}

function TabPanel(props:TabPanelProps):JSX.Element {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

function a11yProps(index:number) {
    return {
        id: `simple-tab-${index}`,
        "aria-controls": `simple-tabpanel-${index}`
    };
}


function AllBox(props:PickProps1):JSX.Element {
    return (
        <>
            {
                props.data1.map((a:Pick1, i:number) => {
                    return (
                        <div className="Pickbox_wrap" key={i}>
                            <Link to={`/detail/${props.res1[i]}`}>
                                <img src={a.imgurl} />
                                <div className="Pickbox_txt">
                                    <p>{a.brand}</p>
                                    <p>{a.title}</p>
                                    <span>{a.persent}</span><span>{a.price}</span><span>{a.saleprice}</span>
                                </div>
                            </Link>
                        </div>
                    )
                })
            }
        </>
    )
}

