export interface TabPanelProps {
    children: React.ReactNode;
    value: number;
    index: number;
    style: {
        width:string;
        margin:string;
    }
}


function NewArrival():JSX.Element {
    const [value, setValue] = useState<number>(0);

    const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
        setValue(newValue);
    };


    let [data1] = useState<Arrival1[]>(ArrivalData1)
    let [data2] = useState<Arrival1[]>(ArrivalData2)
    let [data3] = useState<Arrival1[]>(ArrivalData3)
    let [data4] = useState<Arrival1[]>(ArrivalData4)
    let [data5] = useState<Arrival1[]>(ArrivalData5)
    let [data6] = useState<Arrival1[]>(ArrivalData6)
    let [data7] = useState<Arrival1[]>(ArrivalData7)
    let [data8] = useState<Arrival1[]>(ArrivalData8)

    let [res1, setRes1] = useState<number[]>([0, 1, 2, 3]);
    let [res2, setRes2] = useState<number[]>([4, 5, 6, 7]);
    let [res3, setRes3] = useState<number[]>([8, 9, 10, 11]);
    let [res4, setRes4] = useState<number[]>([12, 13, 14, 15]);
    let [res5, setRes5] = useState<number[]>([16, 17, 18, 19]);
    let [res6, setRes6] = useState<number[]>([20, 21, 22, 23]);
    let [res7, setRes7] = useState<number[]>([24, 25, 26, 27]);
    let [res8, setRes8] = useState<number[]>([28, 29, 30, 31]);

    return (
        <Box sx={{ width: '100%' }}>
            <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
                    <Tab label="여성" {...a11yProps(0)} />
                    <Tab label="남성" {...a11yProps(1)} />
                    <Tab label="잡화/슈즈" {...a11yProps(2)} />
                    <Tab label="골프/스포츠" {...a11yProps(3)} />
                </Tabs>
            </Box>
            <TabPanel value={value} index={0} style={{ width: '1240px', margin: '0 auto' }}>
                <Swiper
                    loop={true}
                    cssMode={true}
                    navigation={true}
                    autoplay={{
                        delay: 2500,
                        disableOnInteraction: false,
                    }}
                    pagination={true}
                    modules={[Navigation, Pagination, Autoplay]}
                    className="mySwiper"
                >
                    <SwiperSlide>
                        <Womenbox1 data1={data1} res1={res1} />
                    </SwiperSlide>
                    <SwiperSlide>
                        <Womenbox2 data2={data2} res2={res2} />
                    </SwiperSlide>
                </Swiper>
            </TabPanel>
            <TabPanel value={value} index={1} style={{ width: '1240px', margin: '0 auto' }}>
                <Swiper
                    loop={true}
                    cssMode={true}
                    navigation={true}
                    autoplay={{
                        delay: 2500,
                        disableOnInteraction: false,
                    }}
                    pagination={true}
                    modules={[Navigation, Pagination, Autoplay]}
                    className="mySwiper"
                >
                    <SwiperSlide>
                        <Menbox1 data3={data3} res3={res3} />
                    </SwiperSlide>
                    <SwiperSlide>
                        <Menbox2 data4={data4} res4={res4} />
                    </SwiperSlide>
                </Swiper>
            </TabPanel>
            <TabPanel value={value} index={2} style={{ width: '1240px', margin: '0 auto' }}>
                <Swiper
                    loop={true}
                    cssMode={true}
                    navigation={true}
                    autoplay={{
                        delay: 2500,
                        disableOnInteraction: false,
                    }}
                    pagination={true}
                    modules={[Navigation, Pagination, Autoplay]}
                    className="mySwiper"
                >
                    <SwiperSlide>
                        <Shoesbox1 data5={data5} res5={res5} />
                    </SwiperSlide>
                    <SwiperSlide>
                        <Shoesbox2 data6={data6} res6={res6} />
                    </SwiperSlide>
                </Swiper>
            </TabPanel>
            <TabPanel value={value} index={3} style={{ width: '1240px', margin: '0 auto' }}>
                <Swiper
                    loop={true}
                    cssMode={true}
                    navigation={true}
                    autoplay={{
                        delay: 2500,
                        disableOnInteraction: false,
                    }}
                    pagination={true}
                    modules={[Navigation, Pagination, Autoplay]}
                    className="mySwiper"
                >
                    <SwiperSlide>
                        <Golfbox1 data7={data7} res7={res7} />
                    </SwiperSlide>
                    <SwiperSlide>
                        <Golfbox2 data8={data8} res8={res8} />
                    </SwiperSlide>
                </Swiper>
            </TabPanel>
        </Box>
    );
}

function TabPanel(props:TabPanelProps) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}


function a11yProps(index:number) {
    return {
        id: `simple-tab-${index}`,
        "aria-controls": `simple-tabpanel-${index}`
    };
}

function Womenbox1(props:Arrival1Props1) {
    return (
        <>
            {
                props.data1.map((a:Arrival1, i:number) => {
                    return (
                        <div className="box_wrap">
                            <Link to={`/detail/${props.res1[i]}`}>
                                <img src={a.imgurl} />
                                <div className="box_wrap_txtbox">
                                    <p>{a.brand}</p>
                                    <p>{a.title}</p>
                                    <span>{a.persent}</span><span>{a.price}</span><span>{a.saleprice}</span>
                                </div>
                            </Link>
                        </div>
                    )
                })
            }
        </>
    )
}

