import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import {Pagination, Navigation } from "swiper";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import "./Stylefinder.css";
import style_data1 from "./styleData1";
import style_data2 from "./styleData2";
import style_data3 from "./styleData3";
import style_data4 from "./styleData4";

// type import
import {Style1} from "./styleData1"
import {Style2} from "./styleData2"
import {Style3} from "./styleData3"
import {Style4} from "./styleData4"

interface styleprops1 {
    stdata1 : Style1[];
}
interface styleprops2 {
    stdata2 : Style2[];
}
interface styleprops3 {
    stdata3 : Style3[];
}
interface styleprops4 {
    stdata4 : Style4[];
}



// import required modules


function Stylefocus():JSX.Element {
    const [stdata1] = useState(style_data1);
    const [stdata2] = useState(style_data2);
    const [stdata3] = useState(style_data3);
    const [stdata4] = useState(style_data4);

    return (
        <>
            <Swiper
                spaceBetween={30}
                centeredSlides={true}
                pagination={{
                    clickable: true,
                }}
                navigation={true}
                modules={[Pagination, Navigation]}
                className="mySwiper4"
            >
                <SwiperSlide>
                    <Stylebox1 stdata1={stdata1} />
                </SwiperSlide>
                <SwiperSlide>
                    <Stylebox2 stdata2={stdata2} />
                </SwiperSlide>
                <SwiperSlide>
                    <Stylebox3 stdata3={stdata3} />
                </SwiperSlide>
                <SwiperSlide>
                    <Stylebox4 stdata4={stdata4} />
                </SwiperSlide>
            </Swiper>
        </>
    );
}



function Stylebox1(props:styleprops1):JSX.Element {
    return (
        <>
            {
                props.stdata1.map((a:Style1, i:number) => {
                    return (
                        <div className="stBox">
                            <img src={a.imgurl} />
                            <span>{a.text}</span>
                        </div>
                    )
                })
            }
        </>
    )
}

function Stylebox2(props:styleprops2):JSX.Element {
    return (
        <>
            {
                props.stdata2.map((a:Style2, i:number) => {
                    return (
                        <div className="stBox">
                            <img src={a.imgurl} />
                            <span>{a.text}</span>
                        </div>
                    )
                })
            }
        </>
    )
}

function Stylebox3(props:styleprops3):JSX.Element {
    return (
        <>
            {
                props.stdata3.map((a:Style3, i:number) => {
                    return (
                        <div className="stBox">
                            <img src={a.imgurl} />
                            <span>{a.text}</span>
                        </div>
                    )
                })
            }
        </>
    )
}

function Stylebox4(props:styleprops4):JSX.Element {
    return (
        <>
            {
                props.stdata4.map((a:Style4, i:number) => {
                    return (
                        <div className="stBox">
                            <img src={a.imgurl} />
                            <span>{a.text}</span>
                        </div>
                    )
                })
            }
        </>
    )
}




export default Stylefocus;
